.Works_wrapperA__2496N{

    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.Works_wrapperA__2496N a{
    display: block;
    width: 200px;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    font-family: sans-serif;
    text-decoration: none;
    color: #333;
    border: 2px solid #333;
    letter-spacing: 2px;
    text-align: center;
    position: relative;
    -webkit-transition: all .35s;
    transition: all .35s;
}

.Works_wrapperA__2496N a span{
    position: relative;
    z-index: 2;
}

.Works_wrapperA__2496N a:after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #04B4AE;
    -webkit-transition: all .35s;
    transition: all .35s;
}

.Works_wrapperA__2496N a:hover{
    color: #fff;
}

.Works_wrapperA__2496N a:hover:after{
    width: 100%;
}

